<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_driver">
            <el-button slot="contentRight" type="primary" @click="auditAny">批量审核</el-button>
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
        <FormAE ref="FormAE" :apis="apis" @refresh="$refs.list.getList()" FormsCode="oa_driver" upLoadBeforSubmit imgNameField="Icon" />
        <FormDetails ref="FormDetails" :apis="apis" FormsCode="oa_driver"></FormDetails>
    </div>
</template>

<script>
export default {
    name: "DriverList",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
        FormAE: () => import("@/universalPage/formAE/FormAE.vue"),
        FormDetails: () => import("@/universalPage/formDetails/FormDetails.vue")
    },
    data() {
        return {
            apis: {
                edit: this._api.VMS_DriverEdit,
                create: this._api.VMS_DriverCreate,
                list: this._api.VMS_DriverManage,
                delete: this._api.VMS_DriverDelete,
                audit: this._api.VMS_DriverAudit,
                details: this._api.VMS_DriverDetails
            },
            fieldToPage: {
                FName: this.toDetails
            }
        }
    },
    methods: {
        render(h) {
            return (
                <div>
                    <el-table-column prop="FName" label="姓名" min-width="100" align="center"></el-table-column>
                    <el-table-column prop="FGender" label="性别" width="60" align="center" ></el-table-column>
                    <el-table-column prop="FBirthDay" label="出生日期" width="100" align="center" formatter={(row) => {
                        return (
                            <span>{row.FBirthDay ? this.$time(row.FBirthDay).format("YYYY-MM-DD") : ""}</span>
                        )
                    }
                    }></el-table-column>
                    <el-table-column prop="FIDNumber" label="身份证号" width="180" align="center" ></el-table-column>
                    <el-table-column prop="Icon" label="照片" width="100" align="center" formatter={(row) => {
                        return (
                            <a href={row.Icon} target="_blank">
                                <img src={row.Icon} style="width:70px" />
                            </a>
                        )
                    }
                    }></el-table-column>
                    <el-table-column prop="FDriversLicense" label="驾驶证类型" width="100" align="center" ></el-table-column>
                    <el-table-column prop="FDriversLicenseStartDate" label="驾驶证获取日期" width="120" align="center" formatter={(row) => {
                        return (
                            <span>{row.FDriversLicenseStartDate ? this.$time(row.FDriversLicenseStartDate).format("YYYY-MM-DD") : ""}</span>
                        )
                    }
                    }></el-table-column>
                    <el-table-column prop="FDriversLicenseIndate" label="驾驶证有效期" width="120" align="center" formatter={(row) => {
                        return (
                            <span>{row.FDriversLicenseIndate ? this.$time(row.FDriversLicenseIndate).format("YYYY-MM-DD") : ""}</span>
                        )
                    }
                    }></el-table-column>
                    <el-table-column prop="FEducation" label="学历" width="60" align="center" ></el-table-column>
                    <el-table-column prop="FProfession" label="专业" width="160" align="center" ></el-table-column>
                    <el-table-column prop="FMobile" label="联系方式" width="120" align="center" ></el-table-column>
                    <el-table-column prop="FAddress" label="家庭住址" width="200" align="center" ></el-table-column>
                    <el-table-column prop="FHiredate" label="入职日期" width="100" align="center" formatter={(row) => {
                        return (
                            <span>{row.FHiredate ? this.$time(row.FHiredate).format("YYYY-MM-DD") : ""}</span>
                        )
                    }
                    }></el-table-column>
                    <el-table-column prop="FContractToDate" label="合同日期" width="100" align="center" formatter={(row) => {
                        return (
                            <span>{row.FContractToDate ? this.$time(row.FContractToDate).format("YYYY-MM-DD") : ""}</span>
                        )
                    }
                    }></el-table-column>

                    <el-table-column prop="CreateName" label="创建人" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="创建时间" width="160" align="center"></el-table-column>
                </div >
            )
        },
        toDetails(row) {
            this.$refs.FormDetails.start(row)
        },
        auditAny() {
            let list = this.$refs.list.chooseList
            if (list.length !== 0) {
                this.$u.confirm("请确认批量审核!").then(() => {
                    this.apis.audit(list.map(it => {
                        return {
                            Id: it.Id,
                            FStatus: 3
                        }
                    })).then(res => {
                        if (res) {
                            this.$u.msg("审核成功!")
                            this.$refs.list.getList()
                        }
                    })
                }).catch(() => { })
            }
        }
    },
    mounted() { }
}
</script>

<style lang="scss" scoped>
</style>